.card {
  width: $card-width;
  padding: 14px;
  border: 1px solid #f3f3f3;

  &__img {
    display: block;
    width: 160px;
    height: 134px;
    margin: 15px auto 45px;
    transition: transform 0.4s;

    &:hover {
      box-sizing: content-box;
      margin-top: 0;
      margin-bottom: 34px;
      padding: 10px 1px;
      transform: scale(1.2);
      border: 3px solid #f3f3f3;
    }
  }

  &__title {
    margin-bottom: 6px;
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
  }

  &__code {
    display: block;
    margin-bottom: 15px;
  }

  &__rating {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }

  &__price-container {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    margin-bottom: 20px;
  }

  &__price-value {
    font-weight: 700;
    font-size: 16px;
  }

  &__button {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    border: 1px solid $main-color;
    border-radius: 5px;
    font-weight: bold;
    font-size: 14px;
    color: #fff;
    text-decoration: none;
    background-color: $main-color;
    transition: color 0.4s, background-color 0.4s;

    &:hover {
      background-color: #fff;
      color: $main-color;
    }
  }
}
